
// practice1 
import { useReducer } from "react"
// import { useState } from "react"

const initialstate={
value:'',
isTouched:false
}

const reducerfunction=(state, action)=>{
    if(action.type=== 'input'){
     return {value: action.value, isTouched: state.isTouched}
    }
    if(action.type=== 'blur'){
    return  {isTouched:true, value:state.value}
    }
    if(action.type=== 'reset'){
      return{ isTouched:false, value:''}
    }
    return reducerfunction;
  }
  
const useCustomhooks=(validatevalue)=>{
  
  const[inputstate, dispatch]= useReducer(reducerfunction, initialstate)
  // const[valueinput, setvalueinput]= useState('')
  // const[valueistouched, setvalueistouched]= useState(false)
  const entervalueisvalid = validatevalue(inputstate.value)
  const haserror= !entervalueisvalid && inputstate.isTouched


  const valuechangehandler=event=>{
    // setvalueinput(event.target.value)
    dispatch({type:'input', value:event.target.value})
  }
  
  const valueblurhandler=()=>{
    // setvalueistouched(true)
    dispatch({type:'blur'})
    }
    
    const reset=()=>{
      // setvalueinput('')
      // setvalueistouched(false)
      dispatch({type:"reset"})
  }
  return{
   value: inputstate.value,
    entervalueisvalid,
    haserror,
    valuechangehandler,
    valueblurhandler,
    reset
  }
}
export default useCustomhooks