import React from 'react'

const Input = (props) => {
  return (
    <>   
    <label htmlFor={props.id}>{props.id}</label>
    <input style={{width:"100%"}}
    type={props.type} 
    id={props.id}
    onChange={props.onChange}    
    ref={props.ref}
    onBlur={props.onBlur}     
    value={props.value}
    />
      
  
    
    </>
  )
}

export default Input
